import ScrollReveal from "scrollreveal";

function scrollInit() {
  const options1 = {
    distance: "20%",
    origin: "bottom",
    opacity: 0,
    delay: 600,
    mobile: false,
  };
  const options2 = {
    distance: "20%",
    origin: "right",
    opacity: 0,
    delay: 600,
    mobile: false,
  };
  const options3 = {
    distance: "20%",
    origin: "bottom",
    opacity: 0,
    delay: 600,
    mobile: false,
  };
  const options4 = {
    distance: "20%",
    origin: "bottom",
    opacity: 0,
    delay: 700,
    mobile: false,
  };
  const options5 = {
    distance: "20%",
    origin: "bottom",
    opacity: 0,
    delay: 800,
    mobile: false,
  };
  const opacity = {
    opacity: 0,
    delay: 300,
    mobile: false,
  };
  ScrollReveal().reveal(".reveal", options1);
  ScrollReveal().reveal(".reveal-right", options2);

  ScrollReveal().reveal(".reveal-delay-100", options3);
  ScrollReveal().reveal(".reveal-delay-200", options4);
  ScrollReveal().reveal(".reveal-delay-300", options5);

  ScrollReveal().reveal(".reveal-opacity", opacity);
}
scrollInit();