import slick from 'slick-carousel';

function sliderCard() {

$('.card-slider-gallery').slick({
  slidesToShow: 1,
  slidesToScroll: 1,
  arrows: false,
  fade: true,
  centerMode: true,
  asNavFor: '.card-slider-thumbnails'
});
$('.card-slider-thumbnails').slick({
  slidesToShow: 3,
  arrows: false,
  slidesToScroll: 1,
  asNavFor: '.card-slider-gallery',
  dots: false,
  centerMode: false,
  focusOnSelect: true
});
}
sliderCard();