function toggleSelect() {
  let items = $('.product__option');
  let label = $('.product__selected');
  Array.from(items).forEach(function(item) {
    $(item).on('click', function() {
      items.removeClass('active')
      $(this).addClass('active');
      $(this).parent().siblings('.product__selected').children('input').val($(this).text());
      $(this).parent().siblings('.product__selected').children('span').text($(this).text());
      label.next().removeClass('active');
    });
  });
  label.on('click', function() {
    $(this).toggleClass('active');
    $(this).next().toggleClass('active');
  });
  $(document).on('mouseup', function(e) {
    if (!label.is(e.target) && label.has(e.target).length === 0 && $('.schedule__filter').has(e.target).length === 0) {
      label.removeClass('active');
      label.next().removeClass('active');
    }
  });
}
toggleSelect();